import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Pessoa_juridica } from '../../../modelo/Pessoa_juridica'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'pessoa_juridica-dados-edit',   templateUrl: 'pessoa_juridica_dadosEdit.component.html' }) export class Pessoa_juridicaDadosEditComponent implements OnInit {    pessoa_juridica: Pessoa_juridica;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.pessoa_juridica.Pessoa_juridica";                 let id = +params['id'];         this.pessoa_juridica = new Pessoa_juridica();         this.pessoa_juridica.cd_pessoa_juridica = id;         o.objetoJson = this.pessoa_juridica;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a pessoa_juridica. ';       }      this.pessoa_juridica = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


